<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div id="divMainLeadData">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('RequestEmployee') }}</span>                       
                        <span class="float-right">
                            <a href="javascript:;" class="p-action-btn text-white" :title="$t('BacktoList')"
                                @click="Close">
                                <em class="fa fa-angle-double-left pr-2" aria-hidden="true"></em>
                                <br />{{ $t('BacktoList') }}
                            </a>
                        </span>
                    </div>
                </div>
            </div>
            <div class="border p-3">
                <Form ref="form">
                    <dynamic-form ref="RequestEmployeeForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'>
                        <template v-slot:tgslot-divLicenses="{ data }">     
                            <div class="listing">
                                <div class="table-responsive table-wrapper-scroll-y my-custom-scrollbar">
                                    <table class="table table-bordered mt-0" style="width:100%;">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th><div class="text-center"><span href="javascript:;" class="textaligncenter">Select Module</span></div></th>
                                                <th><span>Serial Number</span></th>
                                                <th><span>License Name</span></th>
                                                <th><span>Number of Users</span></th>
                                                <th><span>From Date</span></th>
                                                <th><span>To Date</span></th>
                                                <th><span>Request Mode</span></th>
                                                <th><span>Info Mode</span></th>
                                            </tr>
                                        </thead>                   
                                        <tbody>        
                                            <tr class="trrowdata" v-for="(item,index) in divLicensesData" :key="index">                                              
                                                <td class="text-center">
                                                    <label class="form-check form-check-inline">
                                                        <div class="custom-control custom-checkbox custom-control-inline">
                                                            <input :name="'Checkbox_'+item.AVAILABLE_LICENSE_ID" :id="'Checkbox_'+item.AVAILABLE_LICENSE_ID"  type="checkbox" class="chkItems custom-control-input"  :data-licenseid="item.AVAILABLE_LICENSE_ID" :data-moduleMasterID="item.MODULE_MASTER_ID">
                                                            <label :for="'Checkbox_'+item.AVAILABLE_LICENSE_ID" class="custom-control-label"></label>
                                                        </div>
                                                    </label>
                                                </td>
                                                <td class="textaligncenter">{{ index+1 }}</td>
                                                <td>{{item.MODULE_MASTER_NAME}}</td>
                                                <td>
                                                    <Field :name="'txtNoOfUsers' + item.REQUEST_QUANTITY">
                                                        <input type="text" v-model="item.REQUEST_QUANTITY"
                                                            v-bind:class="{ 'form-control': true }"
                                                            v-bind:name="'txtNoOfUsers' + item.REQUEST_QUANTITY">
                                                    </Field>
                                                </td>
                                                <td class="overflow-visible">
                                                    <Field :name="'txtFromDate' + item.AVAILABLE_LICENSE_ID"
                                                        v-slot="{ errors }">                                                        
                                                        <v-date-picker :popover="popover" :minute-increment="5" :masks="masks"
                                                            class="input-group" v-model="item.FROM_DATE"
                                                            v-bind:name="'txtFromDate'+item.AVAILABLE_LICENSE_ID"
                                                            v-bind:value="item.FROM_DATE">
                                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                <input class="form-control" :value="inputValue" v-on="inputEvents" />
                                                                <div @click="togglePopover()" class="input-group-append"><span
                                                                        class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                                </div>
                                                            </template>
                                                        </v-date-picker>
                                                    </Field>
                                                </td>
                                                <td class="overflow-visible">
                                                    <Field :name="'txtFromDate' + item.AVAILABLE_LICENSE_ID"
                                                        v-slot="{ errors }">                                                     
                                                        <v-date-picker :popover="popover" :minute-increment="5" :masks="masks"
                                                            class="input-group" v-model="item.TO_DATE"
                                                            v-bind:name="'txtFromDate'+item.AVAILABLE_LICENSE_ID"
                                                            v-bind:value="item.TO_DATE">
                                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                <input class="form-control" :value="inputValue" v-on="inputEvents" />
                                                                <div @click="togglePopover()" class="input-group-append"><span
                                                                        class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                                </div>
                                                            </template>
                                                        </v-date-picker>
                                                    </Field>
                                                </td>
                                                <td>
                                                    <div class="custom-control custom-checkbox">
                                                        <input class="chkLicense form-check-input text-center custom-control-input" type="checkbox" :name="'chk_'+index" :id="'chk_'+index">
                                                        <label class="custom-control-label universal-custom-control-label pt-1" :for="'chk_'+index">Sharing</label>
                                                    </div>
                                                </td>
                                                <td>
                                                    <div class="custom-control custom-checkbox">
                                                        <input class="chkLicense form-check-input text-center custom-control-input" type="checkbox" :name="'chkk_'+index" :id="'chkk_'+index">
                                                        <label class="custom-control-label universal-custom-control-label pt-1" :for="'chkk_'+index">Visible</label>
                                                    </div>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>   
                        </template>
                    </dynamic-form>
                </Form>                
            </div>
        </div>
        <AssociateCompany v-if="isAssociateCompany"></AssociateCompany>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
import AssociateCompany from '../../../components/Common/Modal/modal/associateCompanyModal.vue'
export default {
    components:{ AssociateCompany },
    data(){
        return{
            isLoading:false,
            divLicensesData:[],
            FormSchema: [
            {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "RadioField",
                            label: this.$t('CompanyType'),
                            name: "CompanyType",
                            value: "Resource",
                            placeholder: "",
                            config: {
                                options: [{
                                    name: 'Company', value: 'Resource'
                                }, {
                                    name: "Vendor", value: 'Service'
                                }],
                                onChange:this.LoadCompanies,
                                showAddIcon: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Linkedcompany'),
                            name: "Linkedcompany",
                            value: "",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onChange:this.ddlLinkedCompany,
                                onAddButtonClick: this.AssociateCompany
                            },
                            placeholder: this.$t('Select Linked Company'),
                            validationRules: { "required": true},
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('RequestType'),
                            name: "RequestType",
                            value: "",
                            placeholder: "",
                            visibility:false,
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange:this.LoadServices
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('ShareYourLicenseMessage'),
                            name: "ShareYourLicenseMessage",
                            value: "Resource",
                            placeholder: "",
                            visibility:false,
                            config: {
                                options: [{
                                    name: 'Yes', value: 'Yes'
                                }, {
                                    name: "No", value: 'No'
                                }],
                                onChange:this.ShareLicenseRadio,
                                showAddIcon: false,
                                
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Project'),
                            name: "Project",
                            value: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange:this.ddlProject
                            },
                            placeholder: this.$t('Select Project'),
                            validationRules: { "required": true},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('SelectService'),
                            name: "SelectService",
                            visibility:false,
                            value: "",
                            config: {
                                options: [],
                                showAddIcon: false
                            },
                            placeholder: this.$t('Select Service'),
                            validationRules: { "required": false},
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ResourceText'),
                            name: "ResourceText",
                            visibility:false,
                            value: "",                                                      
                            validationRules: { "required": true},
                        }
                    ]
                },
                {     
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",                            
                            name: "divLicenses",
                            value: "",
                            visibility:false,
                            validationRules: "",                
                        }
                    ]         
                },
                {     
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            visibility:false,
                            validationRules: { "required": true},             
                        }
                    ]         
                }
            ],
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-paper-plane pr-2'></i>Send Request",
                    isVisible:false
                }               
            ],
            vendorId:null,
            projectId:null,
            RequestID:null,
            isAssociateCompany:false,
            Companytype:null,
            Requesttype:null,
            ProjectId:null,
            linkedcompanyID:null,
            IsShared:null,
            Servicelist:[],
            Requestquantity:[]
        }
    },
    created:function(){                
        var ths=this;
        ths.RequestID=ths.$route.params.RequestID
        ths.linkedcompanyID=ths.$route.params.linkedcompanyID
        if(ths.RequestID !=null){
            ths.GetRequestData();
            ths.SearchLicense();
        }        
        ths.GetLinkedCompanyDetail();
        ths.GetProjectList();
    },
    methods:{
        AssociateCompany:function(){
            this.isAssociateCompany=true;
        },
        GetRequestData:function(){            
            var ths=this;
            var url=`RequestID=${ths.RequestID}`;
            DataService.GetRequestData(url).then((response)=>{                
                if(response.data.result.length>0){ 
                    ths.Companytype=response.data.result[0].company_type;
                    ths.Requesttype=response.data.result[0].request_type;
                    ths.ProjectId=response.data.result[0].PROJECT_ID;
                    ths.FormSchema[0].Data[4].value=response.data.result[0].PROJECT_ID;
                    ths.FormSchema[0].Data[1].value=response.data.result[0].REQUEST_TO_COMPANY_ID;
                    ths.FormSchema[2].Data[0].value=response.data.result[0].DESCRIPTION;
                    ths.IsShared=response.data.result[0].is_shared;
                    if (ths.Companytype==1)
                    {
                        ths.BindCompanyAndVendorList("Company");
                    }
                    else
                    {
                        ths.BindCompanyAndVendorList("Vendor");
                    }
                    if (ths.Requesttype==2)
                    {
                        ths.GetCompanySevices("SERVICES");
                    }

                    if (ths.Requesttype == 1 && ths.IsShared==false)
                    {
                        ths.GetCompanySevices("L_NOSHARE");
                    }                    
                    if(ths.Requesttype == 1){
                        var resource=[{name: 'Resource', value: 'Resource'},{name: "Service", value: 'Service'}];
                        ths.FormSchema[0].Data[2].config.options=resource;                        
                        ths.FormSchema[0].Data[2].value='Resource';                       
                        ths.FormSchema[0].Data[3].visibility=true;
                        ths.FormSchema[0].Data[2].visibility=true;   
                        ths.FormSchema[1].Data[0].visibility=true;                        
                        ths.FormSchema[2].Data[0].visibility=true; 
                        ths.buttons[0].isVisible=false; 
                        if (ths.IsShared) {
                            ths.FormSchema[0].Data[6].visibility=false;
                            ths.FormSchema[0].Data[3].value='Yes';
                        }
                        else {
                            ths.FormSchema[0].Data[6].visibility=true;
                            ths.FormSchema[2].Data[0].visibility=false; 
                            ths.FormSchema[0].Data[3].value='No'; 
                            ths.FormSchema[0].Data[6].value=ths.Requestquantity;
                        }                      
                    }
                    else{
                        var resource=[{name: 'Resource', value: 'Resource'},{name: "Service", value: 'Service'}];
                        ths.FormSchema[0].Data[2].config.options=resource;   
                        ths.FormSchema[0].Data[2].value='Service'; 
                        ths.FormSchema[0].Data[0].value='Service'; 
                        ths.FormSchema[0].Data[2].visibility=true;  
                        ths.FormSchema[0].Data[5].visibility=true;
                        ths.FormSchema[2].Data[0].visibility=true;
                        ths.buttons[0].isVisible=true; 
                    }
                }
            })
        },
        GetCompanySevices:function(Val){
            var ths=this;
            var url=`linkedcompanyID=${ths.linkedcompanyID}&RequestID=${ths.RequestID}&calledFrom=${Val}`;            
            DataService.GetCompanyLicense(url).then((response)=>{                                
                if(response.data.result.length>0){
                    ths.Servicelist=response.data.result[0].SERVICE_ID;
                    ths.FormSchema[0].Data[5].value=ths.Servicelist;
                    ths.Requestquantity=response.data.result[0].REQUEST_QUANTITY;                    
                    ths.ProjectId=ths.FormSchema[0].Data[4].value;
                    ths.vendorId=ths.FormSchema[0].Data[1].value;
                    ths.BindServiceOnProjectChange();
                    ths.$refs.RequestEmployeeForm.UpdateKeyValue();
                }
            })
        },
        BindCompanyAndVendorList:function(Val){
            var ths=this;  
            var url=`UserType=${Val}`
            DataService.GetLinkedCompanyDetail(url).then((response)=>{                 
                if(response.data.result.length>0){
                    var LinkCompanyData=response.data.result;
                    LinkCompanyData.forEach((element,index) => {
                        var obj={
                            name:element.COMPANY_NAME,
                            value:element.RELATED_COMPANY_ID
                        }                        
                        ths.FormSchema[0].Data[1].config.options.push(obj);
                    });                    
                    ths.$refs.RequestEmployeeForm.UpdateKeyValue();
                }
            })
        },
        GetProjectList:function(){
            var ths=this;
            DataService.GetProjectList().then((response)=>{  
                if(response.data.result.length>0){
                    var ProjectData=response.data.result;
                    ProjectData.forEach((element,index) => {
                        var obj={
                            name:element.project_name,
                            value:element.project_id
                        }                        
                        ths.FormSchema[0].Data[4].config.options.push(obj);
                    });                    
                    ths.$refs.RequestEmployeeForm.UpdateKeyValue();
                 }
            })
        },
        GetLinkedCompanyDetail:function(){            
            var ths=this;  
            var url=`UserType=Company`;
            DataService.GetLinkedCompanyDetail(url).then((response)=>{                
                if(response.data.result.length>0){                    
                    var LinkCompanyData=response.data.result;
                    LinkCompanyData.forEach((element,index) => {
                        var obj={
                            name:element.COMPANY_NAME,
                            value:element.RELATED_COMPANY_ID
                        }                        
                        ths.FormSchema[0].Data[1].config.options.push(obj);
                    });                    
                    ths.$refs.RequestEmployeeForm.UpdateKeyValue();
                }
            })
        },
        LoadCompanies:function(x,y,z){             
            var ths=this;  
            ths.isLoading=true;
            ths.FormSchema[0].Data[1].value='';                      
            ths.FormSchema[0].Data[1].config.options=[];
            var UserType = y.value=='Resource'?'Company':'Vendor';
            var url=`UserType=${UserType}`
            ths.FormSchema[0].Data[2].visibility=false;
            ths.FormSchema[0].Data[5].visibility=false;
            ths.FormSchema[1].Data[0].visibility=false;
            ths.FormSchema[2].Data[0].visibility=false;
            ths.FormSchema[0].Data[3].visibility=false;
            ths.FormSchema[0].Data[3].value='';  
            if(UserType=='Vendor'){
                ths.buttons[0].isVisible=true;
            } else{
                ths.buttons[0].isVisible=false;
            }           
            DataService.GetLinkedCompanyDetail(url).then((response)=>{                  
                if(response.data.result.length>0){                    
                    var LinkCompanyData=response.data.result;
                    LinkCompanyData.forEach((element,index) => {
                        var obj={
                            name:element.COMPANY_NAME,
                            value:element.RELATED_COMPANY_ID
                        }                        
                        ths.FormSchema[0].Data[1].config.options.push(obj);
                    });                    
                    ths.$refs.RequestEmployeeForm.UpdateKeyValue();
                    ths.isLoading=false; 
                }
            })
        },
        ddlLinkedCompany:function(x,y,z){ 
            var ths=this;
            ths.vendorId=y.value;            
            if(ths.FormSchema[0].Data[0].value=='Resource'){                                
                var resource=[{name: 'Resource', value: 'Resource'}];
                ths.FormSchema[0].Data[2].visibility=true;
                ths.FormSchema[0].Data[5].visibility=true;
                ths.FormSchema[2].Data[0].visibility=true;
                ths.FormSchema[0].Data[2].config.options=resource;
                ths.buttons[0].isVisible=true;
            }
            if(ths.FormSchema[0].Data[0].value=='Service'){                
                var resource=[{name: 'Resource', value: 'Resource'},{name: "Service", value: 'Service'}];
                ths.FormSchema[0].Data[2].visibility=true;
                ths.FormSchema[0].Data[5].visibility=true;
                ths.FormSchema[2].Data[0].visibility=true;
                ths.FormSchema[0].Data[2].value='Service';
                ths.FormSchema[0].Data[2].config.options=resource;
                
            }
            ths.BindServiceOnProjectChange();
            
        },
        ddlProject:function(x,y,z){ 
            var ths=this
            ths.projectId=y.value;
            ths.BindServiceOnProjectChange()
        },
        LoadServices:function(x,y,z){ 
            var ths=this
            ths.SearchLicense();
            ths.SetDivData(y.value);
        },
        SearchLicense:function(){            
            var ths=this;
            var url=`linkedcompanyID=${ths.linkedcompanyID}&RequestID=${ths.RequestID}&calledFrom=compayLicense`;            
            DataService.GetCompanyLicense(url).then((response)=>{                
                if(response.data.result.length>0){
                    ths.divLicensesData=response.data.result;
                }
            })
        },
        ShareLicenseRadio:function(x,y,z){            
            var ths=this;
            if(y.value=='No'){
                ths.FormSchema[0].Data[6].visibility=true;
                ths.FormSchema[1].Data[0].visibility=false;
            }else{
                ths.FormSchema[0].Data[6].visibility=false;
                ths.FormSchema[1].Data[0].visibility=true;
            }
        },
        SetDivData:function(val){            
            var ths=this
            if(ths.FormSchema[0].Data[0].value='Resource'){                
                if(ths.FormSchema[0].Data[2].value='Resource'){
                    ths.FormSchema[1].Data[0].visibility=true;
                    ths.FormSchema[0].Data[3].visibility=true;
                    ths.FormSchema[0].Data[5].visibility=false;
                    ths.FormSchema[0].Data[3].value='Yes'; 
                }
            }
            else{
                debugger
            }
        },
        BindServiceOnProjectChange:function(){              
            var ths=this
            var url=`vendorId=${ths.vendorId}&projectId=${ths.projectId}`
            DataService.BindVendorServicesByVendorWise(url).then((response)=>{
                if(response.data.result.length>0){                    
                    var LinkCompanyData=response.data.result;
                    LinkCompanyData.forEach((element,index) => {
                        var obj={
                            name:element.category_name,
                            value:element.category_id
                        }                        
                        ths.FormSchema[0].Data[5].config.options.push(obj);
                    });                    
                    ths.$refs.RequestEmployeeForm.UpdateKeyValue();
                    ths.isLoading=false; 
                }

             }) 
        },
        async onSubmit(formfields, val) {
            debugger
            var ths=this;
            ths.isLoading=true;
            var val = "";
            var transfer = 0;
            var infoMode = 0;
            var isvalidate = 1;
            var totalQty = 0;    
            var companyType = formfields.CompanyType;
            var companyTypeVal = 1;           
            var requestType = formfields.RequestType;
            var requestValue = 1;
            var ShareLicense = "";
            var isShared = null;
            var ServiceValue = null;
            var ResourceText = null;
            if (companyType == 'Resource') {
                companyTypeVal = 1;
            }
            else if (companyType == 'Service') {
                companyTypeVal = 2;
            }
            if (requestType == 'Resource') {
                requestValue = 1;
                ShareLicense = formfields.ShareYourLicenseMessage;
                isShared = false;
                if (ShareLicense == 'Yes') {
                    isShared = true;
                }
                else if (ShareLicense == 'No') {
                    isShared = false;
                    if (formfields.ResourceText != "") {
                        ResourceText = formfields.ResourceText;
                    }
                    else {                        
                        ths.ShowAlert(ths.$t("EnterResource"), 'failure', true, ths.$t('Alert'));
                        return false;
                    }
                }
            }
            else if (requestType == 'Service') {
                requestValue = 2;
                isShared = "";
                if (formfields.SelectService == "") {
                    ths.ShowAlert(ths.$t("SelectService"), 'failure', true, ths.$t('Alert'));
                    return false;
                }
                ServiceValue = formfields.SelectService;
                ServiceValue = ServiceValue.toString();                
            }
            var url=`requestLicenses=${val}&linkedcompanyID=${formfields.Linkedcompany}&description=${formfields.Description}&RequestId=${ths.RequestID}&TotalEmp=${totalQty}&IsShared=${isShared}&RequestType=${requestValue}&CompanyType=${companyTypeVal}&ServiceVal=${ServiceValue}&ResourceNumber=${formfields.ResourceText}&projectId=${formfields.Project}`;
            DataService.SaveRequestEmployee(url).then((response)=>{                
                ths.isLoading=false;
                if(response.data.status==1){                    
                    ths.Close();
                }
                else{
                    ths.ShowAlert(ths.$t("AddedError", [ths.$t("RequestEmployee")]), 'failure', true, ths.$t('Alert'));
                }
            });
        },
        Close:function(){
            this.$router.push({ name: 'EmployeeSharingList' });
        }
    }
}
</script>