<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('AssociateCompanies') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" ref="AssociateCompanies"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../../services/DataService';
export default { 
    data(){
        return{
            isLoading:false,  
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            label: this.$t('SelectCompanies'),
                            mode:"tag",
                            name: "SelectCompanies",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Companies'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Associate",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],          
        }
    },
    created:function(){                
       this.BindCompanyDropdown();
    },
    methods:{ 
        BindCompanyDropdown:function(){            
            var ths=this;
            DataService.BindCompanyDropdown().then((response)=>{
                if(response.data.result.length>0){                    
                    var LinkCompanyData=response.data.result;
                    LinkCompanyData.forEach((element,index) => {
                        var obj={
                            name:element.company_name,
                            value:element.company_id
                        }                        
                        ths.FormSchema[0].Data[0].config.options.push(obj);
                    });                    
                    ths.$refs.AssociateCompanies.UpdateKeyValue();
                }
            });
        },
        Close:function(){
            this.$parent.isAssociateCompany=false;
        },
        onSubmit(formfields, val) {            
            var commaSeparatedString = formfields.SelectCompanies.map(function(item) {
                return item.value;
            }).join(',');
            var url=`companies=${commaSeparatedString}`;
            DataService.AssociateCompanies(url).then((response)=>{                
                if(response.data.result.length>0){                    
                    if(response.data.result[0].value>0){
                        this.Close();
                        this.$parent.GetLinkedCompanyDetail();
                    }
                }
            })

        }
    }
}
</script>